.gallery {
  width: auto;
  height: auto;
  padding: 1em;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-flow: row wrap; }
  .gallery .memory {
    lost-column: 1/6 6 1em;
    height: 200px;
    margin-bottom: 1em;
    -webkit-perspective: 800;
    position: relative; }

.card.flipped {
  -webkit-transform: rotatex(-180deg); }

.card {
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
  -webkit-transition: 0.5s; }

.card .face {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-backface-visibility: hidden;
  z-index: 2; }

.card .front {
  position: absolute;
  z-index: 1;
  background: black;
  cursor: pointer; }

.card .back {
  -webkit-transform: rotatex(-180deg);
  background: blue;
  color: white;
  cursor: pointer; }
